/* Daterangepicker
http://www.dangrossman.info/
*/

.daterangepicker{
    background: #fff;
    .calendar{
        .calendar-date{
            border: 1px solid @form-control-border-color;
        }
        .prev, .next, th{
            color: @color-primary;
            text-transform: uppercase;
            font-size: 11px;            
        }
        .month{
            color: #6f7b8a;
            font-size: 13px;            
        }
    }
    td.active, td.active:hover{
        background-color: @color-primary;
        border-color: @color-primary;
    }
    td.in-range{
